import axios from 'axios'

export default {
  state: {
    login: [],
    authSuccess: false
  },

  mutations: {
    setLogin (state, list) {
      state.login = list;
    },

    setAuthSuccessFalse (state) {
      state.authSuccess = false
    },

    setAuthSuccessTrue (state){
      state.authSuccess = true
    }
  },

  actions: {
    signIn(context, credentials){
      axios("/login", {
        params: {
          username: credentials.username,
          password: credentials.password
        },
        method: 'POST'
      }).then(response => {
        console.log(response);
        this.$store.commit('setAuthSuccessTrue')
      })
        .catch(error => {
          console.log(error);
          this.$store.commit('setAuthSuccessFalse')
        })
    }

  }

}




